@import '../../_variables.scss';
.movie {
  background-color: black;
  padding-top: 50px;
  &__wrapper {
    max-width: 1300px;
    margin: 0 auto;
    padding: 0px 0px 0px 15px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    @media (min-width: 500px) {
      min-height: 100vh;
    }
    @media (max-width: 500px) {
      min-height: 100vh;
      flex-direction: column-reverse;
      justify-content: center;
    }

    &::after {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      background: linear-gradient(
        295deg,
        rgba(0, 0, 0, 0) 29%,
        rgba(0, 0, 0, 1) 72%
      );
      @media (max-width: 500px) {
        display: none;
      }
    }
  }
  &__video {
    margin-left: auto;
    iframe {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
      transition: all 2.5s;
      -webkit-transition: all 2.5s;
      -moz-transition: all 2.5s;
      -ms-transition: all 2.5s;
      -o-transition: all 2.5s;
      @media (max-width: 500px) {
        display: none;
      }
    }
  }
  &__poster {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    transition: all 4s;
    -webkit-transition: all 4s;
    -moz-transition: all 4s;
    -ms-transition: all 4s;
    -o-transition: all 4s;

    img {
      width: 100%;
      height: 100%;
      opacity: 1;
      visibility: visible;
      object-fit: cover;
      transition: all 4s;
      -webkit-transition: all 4s;
      -moz-transition: all 4s;
      -ms-transition: all 4s;
      -o-transition: all 4s;
      @media (max-width: 500px) {
        object-fit: contain;
      }
    }
  }
}
.info {
  color: #fff;
  z-index: 10;
  max-width: 500px;
  margin-top: 50px;
  @media (max-width: 500px) {
    margin-top: 10px;
  }
  &__title {
    color: rgba(255, 255, 255, 0.782);
    font-size: 60px;
    font-weight: 500;
    margin-bottom: 30px;
    text-shadow: 0px 0px 6px rgba(255, 255, 255, 0.7);
    @media (max-width: 769px) {
      font-size: 40px;
    }
  }
  &__description {
    font-size: 18px;
    line-height: 1.1;
    color: $mainСolor;
    margin-bottom: 20px;
    margin-top: 10px;
    -webkit-line-clamp: 15;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    max-height: 350px;
    @media (max-width: 769px) {
      font-size: 14px;
    }
  }
  &__buttons {
    position: relative;
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__btn {
    padding: 12px 22px;
    border-radius: 18px;
    -webkit-border-radius: 18px;
    -moz-border-radius: 18px;
    -ms-border-radius: 18px;
    -o-border-radius: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: $mainСolor;
    background-color: rgb(230, 93, 14);
    font-size: 16px;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    &:hover {
      background-color: rgb(192, 78, 11);
      color: #fff;
    }
  }
  &__rating {
    margin-bottom: 10px;
  }
  &__ratingKp {
    color: rgb(7, 187, 7);
    margin-right: 10px;
    font-size: 18px;
  }
  &__year {
    color: $secondColor;
  }
  &__genres {
    color: $secondColor;
    padding-right: 10px;
    margin-bottom: 5px;
  }
  &__movieLength {
    color: rgb(230, 93, 14);
    white-space: nowrap;
  }
}
.poster {
  @media (min-width: 500px) {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
