.modal {
  position: fixed;
  top: 10%;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.95);
  animation-name: appear;
  animation-duration: 300ms;
}

.modal__dialog {
  width: 100%;
  max-width: 1000px;
  background: #3a3636;
  position: relative;
  margin: 0 20px;
  height: 600px;
  text-align: left;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: slide-in;
  animation-duration: 0.5s;
}

.modal__header,
.modal__footer {
  display: flex;
  align-items: center;
  padding: 1rem;
}

.modal__header {
  justify-content: space-between;
  font-size: 1.2rem;
}

.moda__footer {
  border-top: 1px solid #dbdbdb;
  justify-content: flex-end;
}

.modal__close {
  cursor: pointer;
  padding: 1rem;
  margin: -1rem -1rem -1rem auto;
}

.modal__body {
  overflow: auto;
  height: 100%;
}

.modal__content {
  padding: 1rem;
  height: 100%;
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slide-in {
  from {
    transform: translateY(-150px);
  }
  to {
    transform: translateY(0);
  }
}
