.btnAdd {
  color: #7c9dbe;
  cursor: pointer;
  font-size: 18px;
  background: none;
  border: none;

  &:hover::after {
    content: attr(data-title);
    position: absolute;
    left: 20%;
    bottom: 0%;
    z-index: 1;
    background: rgba(245, 245, 245, 0.9);
    font-size: 10px;
    padding: 5px 10px;
    border: 1px solid #333;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
  }
}
